h1,h2,h3,h4,p{
    margin: 0;
}
.app-container{
    height: 100%
}
.landing-title{
    font-size: 50px;
    font-weight: 500;
    color: white;
    text-align: center;
    margin-bottom: 50px;
}
.landing-container{
    margin-top: 30px;
    color : white;
}
.projects-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 70px;
    margin-top: 50px;
    padding: 10px;
}
.project-card{
    width: 50%;
    min-height: 150px;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
    border-radius: 10px;
}
.project-card:hover{
    /* background-color: #1f1f1f; */
    scale: 1.1;
    transition: 0.5s;
    border: 1px solid #4e4c4c85;
}
.project-text{
    display: flex;
    flex-direction: column;
    margin: 0;
    height: 100%;
    gap: 10px;
    width: 50%;
    padding: 5px; 
}
.project-img{
    height: 100%;
    width: 50%;
}
.project-img img{
    height: 100%;
    width: 100%;
    border-radius: 10px;

}
